<template>
  <transition name="slideUp">
    <div class="l-keyboard" v-show="isShow" @click="hide">
      <div class="_inner" @click.stop>
        <div class="l-keyboard-bar">
          <span style="color:#666;" @click="clear">清除输入</span>
          <a class="l-keyboard-bar-done" @click="done">完成</a>
        </div>
        <table>
          <tbody>
            <tr v-for="n in Math.ceil(keys.length / 3)">
              <td v-for="m in 3" class="l-keyboard-key" :class="keys[(n-1) * 3 + (m-1)].cls" @touchstart="clickKey(keys[(n-1) * 3 + (m-1)].value)">
                <span > {{ keys[(n-1) * 3 + (m-1)].label }} </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </transition>
</template>
<script>
module.exports = {
  props: {
    nkType: {
      type: String,
      default: 'digit' // number idcard digit
    },

    nkLength: {
      type: Number,
      default: 18
    }
  },
  data: function() {
    return {
      isShow: false,
      inputValue: '',

      keys: [
        {
          label: '1',
          value: '1'
        }, {
          label: '2',
          value: '2'
        }, {
          label: '3',
          value: '3'
        }, {
          label: '4',
          value: '4'
        }, {
          label: '5',
          value: '5'
        }, {
          label: '6',
          value: '6'
        }, {
          label: '7',
          value: '7'
        }, {
          label: '8',
          value: '8'
        }, {
          label: '9',
          value: '9'
        }, {
          cls: '_dot',
          label: '·',
          value: '.'
        }, {
          label: '0',
          value: '0'
        }, {
          cls: '_del',
          label: '删除',
          value: '-'
        }
      ]
    };
  },
  methods: {
    clickKey: function(val) {
      let valArr = this.inputValue.split('.')

      if(val === '-'){ // 删除键
        this.inputValue = this.inputValue.substring(0, this.inputValue.length - 1)
      }else{
        if(this.inputValue.length < this.nkLength){
          switch(val){
            case '':
              break
            case '.': // 小数点
              if(this.inputValue === ''){
                this.inputValue = '0.'
              }else if(valArr.length === 1){
                this.inputValue += val
              }
              break
            case 'X': // 身份证号的x
              this.inputValue += val  
              break
            default:
              if(valArr.length > 1){
                if(valArr[1].length < 2){
                  this.inputValue += val  
                }
              }else{
                this.inputValue += val
              }
              break
          }
        }
      }
      this.inputEvent()
    },

    show() {
      switch(this.nkType){
        case 'digit':
          this.keys[9].label = '.'
          this.keys[9].value = '.'
          break
        case 'number':
          this.keys[9].label = ''
          this.keys[9].value = ''
          break
        case 'idcard':
          this.nkLength = 18
          this.keys[9].label = 'X'
          this.keys[9].value = 'X'
          break
      }

      this.isShow = true
      if(this.input && this.input.tagName){
        this.inputValue = this.input.value || this.input.innerText
      }
    },

    hide() {
      this.isShow = false
      this.inputEvent()
    },

    done() {
      if(this.inputValue){
        this.inputEvent(Number(this.inputValue).toFixed(2))
      }
      this.hide()
    },

    clear() {
      this.inputValue = ''
      this.inputEvent()
    },
    inputEvent(val) {
      this.$emit('$keyboard:input', val || this.inputValue, this.input, this.data)
    }
  },
  mounted() {
    this.$on('$keyboard:show', (input, data) => {
      this.input = input
      this.data = data
      this.show()
    })
  },
  beforeDestroy() {
    this.hide()
  },
  beforeRouteLeave(to, from, next) {
    this.hide()
    next()
  }
};
</script>
<style scoped lang="less">
.l-keyboard {
  user-select: none;  position: absolute; bottom: 0; left: 0; width: 100%; z-index: 9000;
  ._inner{ position: relative; z-index: 1; background: #fff; } 
  &:before{
    width: 100%; height: 300%; background: rgba(0, 0, 0, 0);
    content: ''; position: absolute; top: -300%; left: 0; z-index: 0; 
  }
  &-bar {
    overflow: hidden;
    background: #f0f1f3;
    border-top: 1px solid #9a9a9c;
    padding: 0.5rem; position: relative; z-index: 1;
    &-done{
      float: right; font-size: 0.75rem; font-weight: 500; padding: 0.5rem; margin: -0.5rem;
    }
  }
  table {
    width: 100%; table-layout: fixed; border: none; margin-top: -0.05rem; 
    td {
      font-size: 1.2rem;
      border-top: 0.05rem solid #ccc; text-align: center; vertical-align: middle; height: 2.6rem; color: #000;
    }
    td + td {
      border-left: 0.05rem solid #ccc;
    }
  }
  &-key:active {
    background: #d1d4dd;
  }

  &-key._dot{
    background: #d1d4dd;
    &:active{
      background: #fff;
    }
  }
  &-key._del{
    background: #d1d4dd url(data:image/png;base64,) no-repeat 50% 50%;
    background-size: 1.6rem; font-size: 0;
    &:active{
      background-color: #fff ;
      background-image: url(data:image/png;base64,); 
    }
  }
}

.slideUp-enter-active, .slideUp-leave-active {
  transform: translate3d(0, 0, 0);
  transition: transform cubic-bezier(0.250, 0.460, 0.450, 0.940) 300ms;
  perspective: 1000;
  backface-visibility: hidden;
}
.slideUp-enter, .slideUp-leave-to{
  transform: translate3d(0, 100%, 0);
}
</style>



